import React, { useState } from "react";
import * as Tone from "tone";
import song from "./noise3.wav";

const AudioPlayer = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [player, setPlayer] = useState(null);
  const [synth, setSynth] = useState(null);
  const scale = Math.floor(Math.random() * 3 - 1);
  const notes = [
    "C",
    "C#",
    "D",
    "D#",
    "E",
    "F",
    "F#",
    "G",
    "G#",
    "A",
    "A#",
    "B",
  ];
  const randomNote = notes[Math.floor(Math.random() * notes.length)];

  const handlePlay = () => {
    if (!isPlaying) {
      const noiseC = new Tone.Player({
        url: song,
        loop: true,
        autostart: true,
        volume: 0,
        fadeOut: 8,
        fadeIn: 8,
      }).toDestination();

      const fmSynth = new Tone.FMSynth({
        harmonicity: 5,
        modulationIndex: 10,
        detune: 1,
        oscillator: {
          type: "sine",
        },
        envelope: {
          attack: Math.random(),
          decay: Math.random(),
          sustain: Math.random(),
          release: Math.random(),
        },
        modulation: {
          type: "sawtooth",
        },
        modulationEnvelope: {
          attack: Math.random(),
          decay: 0.5,
          sustain: Math.random(),
          release: 0.5,
        },
        fadeOut: 8,
        fadeIn: 8,
        volume: -6,
      }).toDestination();

      const pitchShift = new Tone.PitchShift({
        pitch: -3,
        windowSize: 0.1,
        wet: 1,
      }).toDestination();

      noiseC.connect(pitchShift);

      noiseC.playbackRate = Math.random() * 0.25 + 0.1;
      fmSynth.connect(Tone.Destination);

      const panner = new Tone.Panner(0.3).toDestination();

      const lfo = new Tone.LFO({
        type: "sine",
        min: -1,
        max: 1,
        frequency: Math.random(),
      }).start();

      lfo.connect(fmSynth.detune);
      lfo.connect(fmSynth.envelope);
      lfo.connect(fmSynth.modulationIndex);
      lfo.connect(fmSynth.harmonicity);
      lfo.connect(panner.pan);

      noiseC.connect(panner);
      fmSynth.triggerAttack(randomNote + scale);
      const delay = new Tone.Delay(0.9).toDestination();
      noiseC.connect(delay);

      setPlayer(noiseC);
      setSynth(fmSynth);
      setIsPlaying(true);
    } else {
      player.stop();
      synth.triggerRelease();
      setIsPlaying(false);
    }
  };

  return (
    <div>
      <button className="button is-black is-fullwidth" onClick={handlePlay}>
        {isPlaying ? "stop" : "play"}
      </button>
    </div>
  );
};

export default AudioPlayer;
