import React, { useState } from "react";
import * as Tone from "tone";
import songB from "./noise2.wav";

const AudioPlayer = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [player, setPlayer] = useState(null);
  const [synth, setSynth] = useState(null);
  const scale = Math.floor(Math.random() * 2 - 1);
  const notes = [
    "C",
    "C#",
    "D",
    "D#",
    "E",
    "F",
    "F#",
    "G",
    "G#",
    "A",
    "A#",
    "B",
  ];
  const randomNote = notes[Math.floor(Math.random() * notes.length)];

  const handlePlay = () => {
    if (!isPlaying) {
      const noiseB = new Tone.Player({
        url: songB,
        loop: true,
        autostart: true,
        volume: -15,
        fadeOut: 8,
        fadeIn: 8,
      }).toDestination();

      const FMSynth = new Tone.FMSynth({
        harmonicity: 5 * Math.random(),
        modulationIndex: 10,
        detune: 1,
        oscillator: {
          type: "sine",
        },
        envelope: {
          attack: Math.random(),
          decay: Math.random(),
          sustain: Math.random(),
          release: Math.random(),
        },
        modulation: {
          type: "square",
        },
        modulationEnvelope: {
          attack: Math.random(),
          decay: 0.5,
          sustain: Math.random(),
          release: 0.5,
        },
        fadeOut: 8,
        fadeIn: 8,
        volume: -6,
      }).toDestination();

      noiseB.playbackRate = Math.random() * 0.7 + 0.1;

      const pitchShift = new Tone.PitchShift({
        pitch: -5,
        windowSize: 0.1,
        wet: 1,
      }).toDestination();

      noiseB.connect(pitchShift);
      FMSynth.connect(Tone.Destination);

      const panner = new Tone.Panner(0.8).toDestination();

      const lfo = new Tone.LFO({
        type: "sine",
        min: -1,
        max: 1,
        frequency: Math.random() * 0.1,
      }).start();

      const hpf = new Tone.Filter({
        type: "highpass",
        frequency: 3000,
      }).toDestination();

      noiseB.connect(hpf);
      lfo.connect(FMSynth.detune);
      lfo.connect(FMSynth.envelope);
      lfo.connect(FMSynth.modulationIndex);
      lfo.connect(FMSynth.harmonicity);
      lfo.connect(panner.pan);

      noiseB.connect(panner);
      FMSynth.triggerAttack(randomNote + scale);
      const delay = new Tone.Delay(0.7).toDestination();
      noiseB.connect(delay);

      setPlayer(noiseB);
      setSynth(FMSynth);
      setIsPlaying(true);
    } else {
      player.stop();
      synth.triggerRelease();
      setIsPlaying(false);
    }
  };

  return (
    <div>
      <button className="button is-black is-fullwidth" onClick={handlePlay}>
        {isPlaying ? "stop" : "play"}
      </button>
    </div>
  );
};

export default AudioPlayer;
